<template>
    <div class="tipMess">
        <div class="date">
            <div class="title">
                <span>Important Dates</span>
            </div>
            <div class="content">
                <div v-for="(tip,index) in tips" :key="index">
                    <div class="event">{{tip.event}}</div>
                    <div class="date">{{tip.date}}</div>
                </div>
            </div>
        </div>
        <div class="organizers">
            <div class="title">
                <span>Organizers</span>
            </div>
            <div class="content">
                <div v-for="(organizer,index) in organizers" :key="index">
                    <div class="name">{{organizer.name}}</div>
                    <div class="introduce">{{organizer.introduce}}</div>
                </div>
            </div>
        </div>
        <div class="editors">
            <div class="title">
                <span>Invited Editors</span>
            </div>
            <div class="content">
                <div v-for="(editor,index) in editors" :key="index">
                    <div class="name">{{editor.name}}</div>
                    <div class="introduce">{{editor.introduce}}</div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
export default {
    data(){
        return{
            tips:[
                {event:'Meeting time',date:'2019-06-02~2019-06-05'},
                // {event:'Meeting time',date:'2019-6-29'},
            ],
            organizers:[
                {name:'Jianbing Yan',introduce:'Huazhong Agricultural University'},
                {name:'David Jackson',introduce:'Cold Spring Harbor Laboratory'},
            ],
            editors:[
                {name:"Chuanfu An",introduce:'Chuanfu joined Nature Communications in January 2017.He handles manuscripts on plant genetics and biotechnology, microbial biotechnology, and general biotechnological development. '},
                {name:'Xiaofeng cui',introduce:'Xiaofeng is the Deputy Editor-in-Chief of Molecular Plant'},
                {name:'Jun Lyu',introduce:'Jun is a Senior Editor of Nature plants. His research focused on understanding the genetic basis of selected agronomic traits in rice, such as upland adaptation, by means of comparative and evolutionary genomics. '}
            ]
        }
    }
}
</script>

<style scoped>
    .date{
        margin-bottom: 20px;
    }
    .editors{
        margin-top: 20px;
    }
    .title{
        width: 100%;
        height: 50px;
        line-height: 50px;
        background: #409EFF; 
        color: #fff;
        font-size: 22px;
    }
    
    .title span{
        padding-left: 20px;
    }
    .content{
        padding-left: 20px;
    }
    .content .name,.content .event{
        color: black;
        font-weight: 300;
        font-size: 17px;
        margin-top: 20px;
    }
    .content .date,.content .introduce{
        margin-top: 5px;
    }
</style>



